import React,{Component} from 'react'
import DatePicker from 'react-datepicker'
import "react-datepicker/dist/react-datepicker.css";
import axios from 'axios';
export default class CreateExercise extends Component{
    constructor(props){
        super(props);

        this.onChangeUsername = this.onChangeUsername.bind(this);
        this.onChangeDescription = this.onChangeDescription.bind(this);
        this.onChangeDuration = this.onChangeDuration.bind(this);
        this.onChangeDate = this.onChangeDate.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.state = {
            username: '',
            description:'',
            duration:0,
            date: new Date(),
            users:[]
        }
    }
    componentDidMount(){
        axios.get('http://localhost:5000/users')
        .then(resp=>{
            if(resp.data.length > 0){
                this.setState({
                    users: resp.data.map(user=>user.username),
                    username: resp.data[0].username
                })
            }
        })
    }
    onChangeUsername(e){
        this.setState({
            username:e.target.value
        });
    }
    onChangeDescription(e){
        this.setState({
            description:e.target.value
        });
    }
    onChangeDuration(e){
        this.setState({
            duration:e.target.value
        });
    }
    onChangeDate(date){
        this.setState({
            date: date
        });
    }
    onSubmit(e){
        e.preventDefault();

        const exercise = {
            username:this.state.username,
            description:this.state.description,
            duration:this.state.duration,
            date:this.state.date
        }
        console.log(exercise)
        axios.post('http://localhost:5000/exercises/add',exercise)
        .then(res=> console.log(res));

        window.location = '/';
    }
    render(){
        return(
            <div><center>
                <h3>Create New Exercise Log</h3>
                </center>
                <div className="container form">
                <form onSubmit={this.onSubmit}>
                    <div className="form-group">
                    <label>Username:</label>
                    <select className="form-select" aria-label="Default select example" ref="userInput" required value={this.state.username} onChange={this.onChangeUsername} >{
                        this.state.users.map(function(user){
                            return <option
                            key={user}
                            value={user}>{user}
                            </option>;
                        })
                    }</select>

                    </div>
                    <div className="form-group">
                         <label>Description:</label>
                         <input type="text" className="form-control" value={this.state.description} onChange={this.onChangeDescription}></input>
                    </div>
                    <div className="form-group">
                         <label>Duration:</label>
                         <input type="text" className="form-control" value={this.state.duration} onChange={this.onChangeDuration}></input>
                    </div>
                    <div className="form-group">
                         <label>Date:</label>
                         <div>
                         <DatePicker selected={this.state.date}
                         onChange={this.onChangeDate} />
                         </div>
                    </div>
                    <br></br>
                    <center>
                    <div className="form-group"><input type="submit" value="Create Exercise Log" className="btn btn-primary"></input></div>
                    </center>
                </form>
                </div>
            </div>
        )
    }
}